"use client"
import React, { useState, useMemo } from "react"
import { PlusIcon, PencilIcon, TrashIcon, ChevronUpIcon, ChevronDownIcon } from "@heroicons/react/24/solid"
import { MagnifyingGlassIcon } from "@heroicons/react/24/outline"

// --- TYPES AND MOCK DATA ---

type Job = {
    id: string;
    serviceName: string;
    customerName: string;
    providerName: string;
    scheduledTime: string; // ISO date string
    status: 'Scheduled' | 'In Progress' | 'Completed' | 'Cancelled';
    cost: number;
    paymentStatus: 'Paid' | 'Unpaid';
    location: string;
    createdAt: string; // ISO date string
};

const mockJobs: Job[] = [
    { id: 'JOB001', serviceName: 'Dental Check-up', customerName: 'Bucky Barnes', providerName: 'Dr. Smith', scheduledTime: '2023-10-30T16:00:00Z', status: 'Completed', cost: 150.00, paymentStatus: 'Paid', location: 'New York, NY', createdAt: '2023-10-26T08:20:00Z' },
    { id: 'JOB002', serviceName: 'Personal Training Session', customerName: 'Jane Smith', providerName: 'Trainer Mike', scheduledTime: '2023-11-02T10:00:00Z', status: 'Scheduled', cost: 75.00, paymentStatus: 'Unpaid', location: 'Los Angeles, CA', createdAt: '2023-10-28T11:00:00Z' },
    { id: 'JOB003', serviceName: 'Deep Tissue Massage', customerName: 'John Doe', providerName: 'Masseuse Anna', scheduledTime: '2023-11-01T14:30:00Z', status: 'In Progress', cost: 90.00, paymentStatus: 'Unpaid', location: 'Chicago, IL', createdAt: '2023-10-29T09:15:00Z' },
    { id: 'JOB004', serviceName: 'Yoga Class', customerName: 'Sam Wilson', providerName: 'Yogi Master', scheduledTime: '2023-10-29T18:00:00Z', status: 'Completed', cost: 25.00, paymentStatus: 'Paid', location: 'Washington, D.C.', createdAt: '2023-10-25T12:00:00Z' },
    { id: 'JOB005', serviceName: 'Luxury Manicure', customerName: 'Wanda Maximoff', providerName: 'Nail Tech Sarah', scheduledTime: '2023-11-03T11:00:00Z', status: 'Scheduled', cost: 45.00, paymentStatus: 'Paid', location: 'Westview, NJ', createdAt: '2023-10-30T14:00:00Z' },
    { id: 'JOB006', serviceName: 'Deep Tissue Massage', customerName: 'Peter Parker', providerName: 'Masseuse Anna', scheduledTime: '2023-10-28T13:00:00Z', status: 'Cancelled', cost: 90.00, paymentStatus: 'Unpaid', location: 'New York, NY', createdAt: '2023-10-26T10:00:00Z' },
];


// --- HELPER COMPONENTS ---

const JobStatusBadge = ({ status }: { status: Job['status'] }) => {
    const styles = {
        Scheduled: 'bg-blue-100 text-blue-800',
        'In Progress': 'bg-yellow-100 text-yellow-800',
        Completed: 'bg-green-100 text-green-800',
        Cancelled: 'bg-gray-100 text-gray-800',
    };
    return <span className={`px-2 py-1 rounded-full text-xs font-medium ${styles[status]}`}>{status}</span>;
};

const PaymentStatusBadge = ({ status }: { status: Job['paymentStatus'] }) => {
    const styles = {
        Paid: 'bg-green-100 text-green-800',
        Unpaid: 'bg-red-100 text-red-800',
    };
    return <span className={`px-2 py-1 rounded-full text-xs font-medium ${styles[status]}`}>{status}</span>;
};


// --- MAIN PAGE COMPONENT ---

export default function JobsPage() {
    const [jobs, setJobs] = useState(mockJobs);
    const [searchTerm, setSearchTerm] = useState("");
    const [sortConfig, setSortConfig] = useState<{ key: keyof Job; direction: 'asc' | 'desc' } | null>(null);

    const filteredAndSortedJobs = useMemo(() => {
        let sortedJobs = [...jobs];

        if (sortConfig !== null) {
            sortedJobs.sort((a, b) => {
                if (a[sortConfig.key] < b[sortConfig.key]) {
                    return sortConfig.direction === 'asc' ? -1 : 1;
                }
                if (a[sortConfig.key] > b[sortConfig.key]) {
                    return sortConfig.direction === 'asc' ? 1 : -1;
                }
                return 0;
            });
        }

        return sortedJobs.filter(job =>
            job.serviceName.toLowerCase().includes(searchTerm.toLowerCase()) ||
            job.customerName.toLowerCase().includes(searchTerm.toLowerCase()) ||
            job.providerName.toLowerCase().includes(searchTerm.toLowerCase())
        );
    }, [jobs, searchTerm, sortConfig]);

    const requestSort = (key: keyof Job) => {
        let direction: 'asc' | 'desc' = 'asc';
        if (sortConfig && sortConfig.key === key && sortConfig.direction === 'asc') {
            direction = 'desc';
        }
        setSortConfig({ key, direction });
    };
    
    const jobColumns = [
        { key: 'id', label: 'Job ID', width: '100px' },
        { key: 'serviceName', label: 'Service', searchable: true, sortable: true },
        { key: 'customerName', label: 'Customer', searchable: true, sortable: true },
        { key: 'providerName', label: 'Provider', searchable: true, sortable: true },
        { key: 'scheduledTime', label: 'Scheduled For', type: 'datetime', sortable: true },
        { key: 'status', label: 'Status', type: 'badge', filterable: true },
        { key: 'cost', label: 'Cost', type: 'currency', sortable: true },
        { key: 'paymentStatus', label: 'Payment', type: 'badge', filterable: true },
        { key: 'location', label: 'Location', searchable: true },
        { key: 'createdAt', label: 'Booked On', type: 'datetime', sortable: true },
        { key: 'actions', label: 'Actions', type: 'actions' }
    ];

    const renderCell = (job: Job, key: string) => {
        const cellValue = job[key as keyof Job];

        switch (key) {
            case 'serviceName':
            case 'customerName':
            case 'providerName':
                return <div className="font-medium text-gray-900">{String(cellValue)}</div>;
            case 'scheduledTime':
            case 'createdAt':
                return <span className="text-sm text-gray-600">{new Date(cellValue as string).toLocaleString()}</span>;
            case 'status':
                return <JobStatusBadge status={job.status} />;
            case 'cost':
                return <span className="text-sm text-gray-800">${(cellValue as number).toFixed(2)}</span>;
            case 'paymentStatus':
                return <PaymentStatusBadge status={job.paymentStatus} />;
            case 'actions':
                return (
                    <div className="flex items-center space-x-3">
                        <button className="text-blue-600 hover:text-blue-900 transition-colors"><PencilIcon className="h-5 w-5" /></button>
                        <button className="text-red-600 hover:text-red-900 transition-colors"><TrashIcon className="h-5 w-5" /></button>
                    </div>
                );
            default:
                return <span className="text-sm text-gray-600">{String(cellValue)}</span>;
        }
    };

    return (
        <div className="container mx-auto px-4 py-8 bg-gray-50 min-h-screen">
            <div className="flex justify-between items-center mb-8">
                <h1 className="text-3xl font-bold text-gray-800">Job Management</h1>
                <button className="bg-blue-600 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded-lg shadow-md hover:shadow-lg transition-all duration-200 ease-in-out flex items-center">
                    <PlusIcon className="h-5 w-5 mr-2" />
                    Add Job
                </button>
            </div>

            <div className="bg-white rounded-xl shadow-lg p-6">
                <div className="flex items-center mb-4">
                    <div className="relative w-full max-w-md">
                        <div className="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none">
                            <MagnifyingGlassIcon className="h-5 w-5 text-gray-400" />
                        </div>
                        <input
                            type="text"
                            placeholder="Search by service, customer, or provider..."
                            className="w-full pl-10 pr-4 py-2 border border-gray-300 rounded-lg focus:ring-blue-500 focus:border-blue-500 transition-colors"
                            onChange={(e) => setSearchTerm(e.target.value)}
                        />
                    </div>
                </div>

                <div className="overflow-x-auto">
                    <table className="min-w-full divide-y divide-gray-200">
                        <thead className="bg-gray-50">
                            <tr>
                                {jobColumns.map((col) => (
                                    <th 
                                        key={col.key} 
                                        scope="col" 
                                        className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider"
                                        onClick={() => col.sortable && requestSort(col.key as keyof Job)}
                                    >
                                        <div className="flex items-center">
                                            {col.label}
                                            {col.sortable && (
                                                <span className="ml-1">
                                                    {sortConfig?.key === col.key ? (
                                                        sortConfig.direction === 'asc' ? <ChevronUpIcon className="h-4 w-4" /> : <ChevronDownIcon className="h-4 w-4" />
                                                    ) : (
                                                        <ChevronUpIcon className="h-4 w-4 text-gray-300" />
                                                    )}
                                                </span>
                                            )}
                                        </div>
                                    </th>
                                ))}
                            </tr>
                        </thead>
                        <tbody className="bg-white divide-y divide-gray-200">
                            {filteredAndSortedJobs.map((job) => (
                                <tr key={job.id} className="hover:bg-gray-50 transition-colors">
                                    {jobColumns.map(col => (
                                        <td key={col.key} className="px-6 py-4 whitespace-nowrap">
                                            {renderCell(job, col.key)}
                                        </td>
                                    ))}
                                </tr>
                            ))}
                        </tbody>
                    </table>
                </div>
            </div>
        </div>
    )
}
